.nav {
  &__list {
    display: flex;
  }

  &__link {
    display: block;
    line-height: 60px;
    font-weight: 500;
    font-size: 12px;

    text-transform: uppercase;
    color: $color-main-accent;

    transition: color 0.3s;

    &:hover {
      color: $color-blue-accent;
    }

    &.is-active {
      position: relative;
      color: $color-blue-accent;

      &::after {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        content: "";
        height: 4px;

        background-color: $color-blue-accent;
        border-radius: 8px;
      }
    }
  }

  &__item {
    margin-right: 20px;

    &:last-child {
      margin-right: 0;
    }
  }
}
